import Carousel from 'react-bootstrap/Carousel';
import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { NavLink } from "react-router-dom";
import Ad1 from "./pic/ad1.png"
import Ad2 from "./pic/ad2.png"
import Ad3 from "./pic/ad3.png"
import gin from "./pic/gin.png"
import vodka from "./pic/vodka.jpg"
import brandy from "./pic/brandy.jpeg"
import rum from "./pic/rum.jpg"
import whiskey from "./pic/whiskey.jpg"
import tequila from "./pic/tequila.jpg"
import liqueur from "./pic/liqueur.jpg"
import softdrinks from "./pic/softdrinks.jpg"
import waitforupdate from "./pic/waitforupdate.jpg"
import "./css/HomePage.css"

function HomeCarousel() {
    return (
        <div>
            <div className='CarouselCss'>
            
                <Carousel>
                    <Carousel.Item interval={3000} >
                        <img src={Ad1} alt="ad1"/>
                        <Carousel.Caption >
                            <div className="CarouselAd1Text">
                                <h4 style={{fontWeight:"lighter"}}></h4>
                                <h1 style={{fontWeight:"bold", marginBottom:"15px"}}>12</h1>
                                <h4></h4>
                                <h4></h4><br/>
                                <NavLink to="/WhiskeyPage/e52220"><Button variant="outline-light" style={{padding:"10px", marginLeft:"10px"}}></Button>{' '}</NavLink>
                            </div>
                        </Carousel.Caption>
                    </Carousel.Item>

                    <Carousel.Item interval={3000} >
                        <img src={Ad2} alt="ad2"/>
                        <Carousel.Caption >
                            <div className="CarouselAd2Text">
                                <h4 style={{fontWeight:"lighter"}}></h4>
                                <h1 style={{fontWeight:"bold", marginBottom:"15px"}}></h1>
                                <h4></h4><br/>
                                <NavLink to="/VodkaPage/b60101"><Button variant="outline-light" style={{padding:"10px", marginLeft:"10px"}}></Button>{' '}</NavLink>
                            </div>
                        </Carousel.Caption>
                    </Carousel.Item>

                    <Carousel.Item interval={3000} >
                        <img src={Ad3} alt="ad3"/>
                        <Carousel.Caption >
                            <div className="CarouselAd3Text">
                                <h4 style={{fontWeight:"lighter"}}></h4>
                                <h1 style={{fontWeight:"bold", marginBottom:"15px"}}> 12</h1>
                                <h4></h4><br/>
                                <NavLink to="/WhiskeyPage/e27777"><Button variant="outline-light" style={{padding:"10px", marginLeft:"10px"}}></Button>{' '}</NavLink>
                            </div>
                        </Carousel.Caption>
                    </Carousel.Item>


                </Carousel>
                
            </div>
            <div >
                <div className='Dividerline'></div>
                <Container style={{marginTop:"10px"}}>
                    <Row>
                        <Col className='Category' style={{backgroundImage: `url(${gin})`}}><NavLink to="/GinPage"></NavLink></Col>
                        <Col className='Category' style={{backgroundImage: `url(${vodka})`}}><NavLink to="/VodkaPage"></NavLink></Col>
                        <Col className='Category' style={{backgroundImage: `url(${brandy})`}}><NavLink to="/BrandyPage"></NavLink></Col>
                    </Row>
                    <Row>
                        <Col className='Category' style={{backgroundImage: `url(${rum})`}}><NavLink to="/RumPage"></NavLink></Col>
                        <Col className='Category' style={{backgroundImage: `url(${whiskey})`}}><NavLink to="/WhiskeyPage"></NavLink></Col>
                        <Col className='Category' style={{backgroundImage: `url(${tequila})`}}><NavLink to="/TequilaPage"></NavLink></Col>
                    </Row>
                    <Row>
                        <Col className='Category' style={{backgroundImage: `url(${liqueur})`}}><NavLink to="/LiqueurPage"></NavLink></Col>
                        <Col className='Category' style={{backgroundImage: `url(${softdrinks})`}}><NavLink to="/SoftDrinksPage"></NavLink></Col>
                        <Col className='Category' style={{backgroundImage: `url(${waitforupdate})`}}><NavLink to="#"></NavLink></Col>
                    </Row>
                </Container>
            </div>
        </div>

    );
  }

  export default HomeCarousel