import "./css/Registersucceed.css"
import Registerpng from "./pic/checked.png"
import Button from 'react-bootstrap/Button';
import { NavLink } from "react-router-dom";


const Succceed = () =>{
    return(
        <div className="RegisterSucceed">
            <div className="RegisterSucceed_Title"><img src={Registerpng} alt="" /><span></span></div>
            <div className='RegisterSucceed_line'></div>
            <div className="RegisterSucceed_detail"><span></span></div>
            <Button className="RegisterSucceed_ReturnHome" variant="dark"><NavLink to="/LoginPage"><span></span></NavLink></Button>
            
        </div>
    );
}

export default Succceed;