import Frame from "../../BasicFrame/frame";

const style={
    position:"relative",
    display:"block",
    paddingTop:"50px",
    paddingLeft:"100px",
    paddingRight:"100px",
    justifyContent:"left",
    height:"1000px"
}

const DeliveryPage = () =>{
    return(
        
        <div style={{position:"relative"}}>
            <Frame/>
            <div style={style}>

                <h1></h1>
                <hr/><br/>
                <h2><b> $2,000 !<br/></b></h2>
                <hr/>
                <h5>$2000:<ul>
                    <li>$150/</li>
                    <li></li>
                </ul></h5>


            </div>
            
        </div>
    );
}

export default DeliveryPage;