import "./css/checksucceed.css"
import checkpng from "./pic/checked.png"
import Button from 'react-bootstrap/Button';
import { NavLink } from "react-router-dom";


const Succceed = () =>{
    return(
        <div className="CheckSucceed">
            <div className="CheckSucceed_Title"><img src={checkpng} alt="" /><span></span></div>
            <div className='CheckSucceed_line'></div>
            <div className="CheckSucceed_detail"><span></span></div>
            <Button className="CheckSucceed_ReturnHome" variant="dark"><NavLink to="/"><span></span></NavLink></Button>
            
        </div>
    );
}

export default Succceed;