import DrunkDrive from "./pic/drunk_driving.png"
import "./css/frame.css"
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { Container } from "react-bootstrap";
import 'bootstrap/dist/css/bootstrap.min.css';
import logo from "./pic/logo_white.png"
import { NavLink } from "react-router-dom";
import Cookies from 'js-cookie';

const visier = () => {
    // Cookies.set('visiter_counter', 0, { path: '/' })
    var allowed;
    //  cookie 
    if (Cookies.get('visiter_counter')) 
    {
        if(allowed === true){
            let currentValue = parseFloat(Cookies.get('visiter_counter'))
            Cookies.set('visiter_counter', currentValue + 0.5, {path: '/'})
            allowed = false
        }
    } 
    else 
    {
        Cookies.set('visiter_counter', 1, {path: '/'})
        allowed = true;
    }

    return Cookies.get('visiter_counter')
}

const visiter_style={
    fontWeight : "bold",
    borderColor : "white",
    borderStyle : "solid",
    borderWidth : "1px",
    padding : "5px",
    margin: "0 auto"

}

const BottomBar = () =>{
    return(
        <>
            <div className="BottomBarLogoCss">
                <img src={DrunkDrive} alt="DrunkDrive"></img>
            </div>
            <div className="BottomBarAreaCss">
                

                <Container className="BottomText">
                    <Row>
                        <Col>
                            <br/><NavLink to="/"><img src={logo} alt="logo" className="BottomLogo"/></NavLink>
                            <br/><br/>MY920072011</Col>
                        <Col>
                            <br/><b></b>
                            <ul>
                                <li><NavLink to="/GinPage"></NavLink></li>
                                <li><NavLink to="/VodkaPage"></NavLink></li>
                                <li><NavLink to="/BrandyPage"></NavLink></li>
                                <li><NavLink to="/RumPage"></NavLink></li>
                                <li><NavLink to="/WhiskeyPage"></NavLink></li>
                                <li><NavLink to="/TequilaPage"></NavLink></li>
                                <li><NavLink to="/LiqueurPage"></NavLink></li>
                                <li><NavLink to="/SoftDrinksPage"></NavLink></li>
                            </ul>
                        </Col>
                        <Col>
                            <br/><b></b><br/>
                            <ul>
                                <li><NavLink to="/IntroductionPage"></NavLink></li>     
                                <li><NavLink to="/DeliveryPage"></NavLink></li>

                            </ul>
                            
                        </Col>
                        <Col>
                            <br/><b></b><br/><br/>(02)8792-5186<br/>-09:0018:00<br/>09:3018:00<br/>10:3018:00<br/>()<br/><br/><br/><br/><br/><span style={visiter_style}> {visier()} </span>
                        </Col>
                    </Row>
                    <Row>
                        <br/><hr/>  
                    </Row>
                </Container>    
                
            </div>
        </>
    );
}
// 468 70
export default BottomBar