import "./css/OtherPage.css";
import Button from 'react-bootstrap/Button';
import { NavLink } from "react-router-dom";
import { useForm } from 'react-hook-form';
import { useNavigate } from 'react-router-dom';
import { useState, useEffect } from "react";
import axios from "axios";
import { useContext } from 'react';
import { LoginContext, AccountContext} from "../../../ContextAPI";


const LoginBox = () =>{
    const { login, setLogin } = useContext(LoginContext);
    const { userAccount, setUserAccount} = useContext(AccountContext);
    
    const navigate = useNavigate();

    const { register, handleSubmit, setError, formState: { errors } } = useForm();
    
    useEffect(() => {
        if (userAccount) {
            console.log("mId:", userAccount);
            navigate('/');
        }
    }, [userAccount]);

    const onSubmit = (data) => {
        
        console.log(data);

        const url = 'http://localhost:3001/login/verify';
        const info = {
            mAccount : data.inputAccount,
            mPwd: data.inputPassword
        }

        axios.post(url, info)
        .then(
            response=>{           
                if(response.data.length > 0){
                    if(info.mAccount === "admin" && info.mPwd === "admin123456"){
                        alert('');
                        setLogin(2);
                        setUserAccount(data.inputAccount);
                        console.log(data.inputAccount)
                        navigate('/AdminPage');
                    }
                    else {
                        console.log(response.data[0].mId)
                        setUserAccount(response.data[0].mId);
                        
                        console.log("");
                        setLogin(1);
                        //
                    }
                }
                else if(response.data.result === "Login failed"){
                    setError("inputPassword",{type:"custom", message:""})
                    // alert("");
                }
            }
        ).catch(
            error =>{
                console.log(error);
                alert("");
                throw error;
            }
        )
        
    }

    return(
        <div className="loginBoxCss">
            <div className="loginContent">
                <div style={{fontSize:"18px", margin:"40px"}}><br/></div>
                <form name="login" onSubmit={handleSubmit(onSubmit)}>
                    <div className="loginForm">
                        <b></b> 
                        <input type="account" id="inputAccount" 
                        {...register("inputAccount", {required: true, maxLength: {value: 10, message: ""}})} />
                        {!!errors.inputAccount && <p>{errors.inputAccount.message.toString() || ""}</p> }

                        <b></b>
                        <input type="password" id="inputPassword" 
                        {...register("inputPassword", {required: true})} />
                        {!!errors.inputPassword && <p>{errors.inputPassword.message.toString() || ""}</p> }
                        
                        <Button variant="danger"  type="submit"  ></Button>
                        <div className="registerLink">
                            ?<NavLink to="/RegisterPage"></NavLink>
                        </div>
                    </div>
                </form>
            </div>
        </div>
    );
}


export default LoginBox;